import React, { useState, useEffect } from "react";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import Page from "@/components/Page";
import Header from "@/components/Header";
// Removed Loading import - using skeleton loading instead
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Checkbox } from "@/components/ui/checkbox";
import { Switch } from "@/components/ui/switch";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Shield, Users, Settings, Plus, Edit, Trash2, Save, RefreshCw, Key, CheckCircle, Eye, UserPlus, Edit3, Trash } from "lucide-react";
import ProtectedRoute from "@/components/ProtectedRoute";
import PermissionRoute from "@/components/permissions/PermissionRoute";
import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

interface Role {
  id: number;
  name: string;
  display_name: string;
  description: string;
  is_system_role: boolean;
  is_active: boolean;
  created_at: string;
  updated_at: string;
}

interface PagePermission {
  id: number;
  page_id: number;
  page_path: string;
  page_name: string;
  page_category: string;
  icon: string | null;
  sort_order: number;
  is_public: boolean;
  can_view: boolean;
  can_add: boolean;
  can_update: boolean;
  can_delete: boolean;
  has_permission: boolean;
}

// API Functions
const fetchRoles = async (): Promise<Role[]> => {
  const response = await axiosInstance.get("/api/roles");
  // Handle both old and new API response formats
  if (response.data.success && response.data.data) {
    return response.data.data;
  }
  // Fallback for old format
  return Array.isArray(response.data) ? response.data : [];
};

const fetchPagesForRole = async (roleId: number): Promise<PagePermission[]> => {
  const response = await axiosInstance.get(`/api/role-page-permissions/role/${roleId}`);
  return response.data.success ? response.data.data : [];
};

const fetchAllPages = async (): Promise<PagePermission[]> => {
  const response = await axiosInstance.get("/api/role-page-permissions/pages");
  return response.data.success ? response.data.data : [];
};

const RolePermissionManagement: React.FC = () => {
  const [selectedRole, setSelectedRole] = useState<Role | null>(null);
  const [pagePermissions, setPagePermissions] = useState<PagePermission[]>([]);
  const [isUpdating, setIsUpdating] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [newRole, setNewRole] = useState({
    name: "",
    display_name: "",
    description: ""
  });

  const queryClient = useQueryClient();

  // Queries
  const { data: roles, isLoading: rolesLoading } = useQuery({
    queryKey: ["roles"],
    queryFn: fetchRoles,
  });

  const { data: allPages, isLoading: pagesLoading } = useQuery({
    queryKey: ["allPages"],
    queryFn: fetchAllPages,
  });

  // Load role permissions when role is selected
  useEffect(() => {
    if (selectedRole && allPages) {
      const loadRolePermissions = async () => {
        try {
          const rolePages = await fetchPagesForRole(selectedRole.id);
          
          // Merge with all pages to show complete list
          const mergedPages = allPages.map(page => {

           
            const rolePage = rolePages.find(rp => rp.page_id === page.id);
            const hasAnyPermission = rolePage && (rolePage.can_view || rolePage.can_add || rolePage.can_update || rolePage.can_delete);
            return {
              ...page,
              page_id: page.id,
              can_view: rolePage?.can_view || false,
              can_add: rolePage?.can_add || false,
              can_update: rolePage?.can_update || false,
              can_delete: rolePage?.can_delete || false,
              has_permission: !!hasAnyPermission
            };
          });
          
          setPagePermissions(mergedPages);
        } catch (error) {
          console.error("Error loading role permissions:", error);
          toast.error("Failed to load role permissions");
        }
      };

      loadRolePermissions();
    }
  }, [selectedRole, allPages]);

  // Mutations
  const updateRolePermissionsMutation = useMutation({
    mutationFn: async ({ roleId, permissions }: { roleId: number; permissions: any[] }) => {
      const response = await axiosInstance.put(`/api/role-page-permissions/role/${roleId}`, {
        pagePermissions: permissions
      });
      return response.data;
    },
    onSuccess: () => {
      toast.success("Role permissions updated successfully");
      queryClient.invalidateQueries({ queryKey: ["rolePermissions"] });
    },
    onError: (error: any) => {
      toast.error(error.response?.data?.message || "Failed to update role permissions");
    }
  });

  const createRoleMutation = useMutation({
    mutationFn: async (roleData: { name: string; display_name: string; description: string }) => {
      const response = await axiosInstance.post("/api/roles", roleData);
      return response.data;
    },
    onSuccess: () => {
      toast.success("Role created successfully");
      queryClient.invalidateQueries({ queryKey: ["roles"] });
      setIsCreateDialogOpen(false);
      setNewRole({ name: "", display_name: "", description: "" });
    },
    onError: (error: any) => {
      toast.error(error.response?.data?.message || "Failed to create role");
    }
  });

  // Handler functions
  const handleRoleSelect = (role: Role) => {
    setSelectedRole(role);
  };

  const handlePagePermissionToggle = (pageId: number, permissionType: 'can_view' | 'can_add' | 'can_update' | 'can_delete') => {
    setPagePermissions(prev => prev.map(page => {
      if (page.page_id === pageId) {
        // Only allow permission changes if the page is enabled
        if (!page.has_permission) {
          return page;
        }

        // Simply toggle the specific permission
        return {
          ...page,
          [permissionType]: !page[permissionType]
        };
      }
      return page;
    }));
  };

  const handlePageToggle = (pageId: number) => {
    setPagePermissions(prev => prev.map(page => {
      if (page.page_id === pageId) {
        const isCurrentlyEnabled = page.has_permission || page.can_view;
        if (isCurrentlyEnabled) {
          // If disabling the page, disable all permissions
          return {
            ...page,
            has_permission: false,
            can_view: false,
            can_add: false,
            can_update: false,
            can_delete: false,
          };
        } else {
          // If enabling the page, just mark as having permission (don't auto-enable any specific permissions)
          return {
            ...page,
            has_permission: true,
            can_view: false,
            can_add: false,
            can_update: false,
            can_delete: false,
          };
        }
      }
      return page;
    }));
  };

  const handleUpdatePermissions = async () => {
    if (!selectedRole) return;

    setIsUpdating(true);
    try {
      const permissionsToUpdate = pagePermissions
        .filter(page => page.has_permission)
        .map(page => ({
          pageId: page.page_id,
          canView: page.can_view,
          canAdd: page.can_add,
          canUpdate: page.can_update,
          canDelete: page.can_delete
        }));

      await updateRolePermissionsMutation.mutateAsync({
        roleId: selectedRole.id,
        permissions: permissionsToUpdate
      });
    } finally {
      setIsUpdating(false);
    }
  };

  const handleCreateRole = () => {
    createRoleMutation.mutate(newRole);
  };

  const filteredRoles = Array.isArray(roles) ? roles.filter(role =>
    role.display_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    role.name.toLowerCase().includes(searchQuery.toLowerCase())
  ) : [];

  // Sort pages by sort_order for flat display
  const sortedPages = (pagePermissions || []).sort((a, b) => (a.sort_order || 0) - (b.sort_order || 0));

  // Removed loading screen for better UX

  return (
    <PermissionRoute requireSuperAdmin>
      <Page>
        <Header
          title="Role & Permission Management"
          buttonText="Create Role"
          action={() => setIsCreateDialogOpen(true)}
          filterByName={true}
          filterWord={searchQuery}
          onFilterChange={setSearchQuery}
          useSheet={false}
          showViewToggle={false}
          showFilters={false}
        />

        {/* Split Layout Container */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 h-[calc(100vh-200px)]">
          {/* Left Panel - Roles List */}
          <div className="lg:col-span-1">
            <Card className="h-full">
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Shield className="h-5 w-5" />
                  Roles ({filteredRoles.length})
                </CardTitle>
                <CardDescription>
                  Select a role to manage its page permissions
                </CardDescription>
              </CardHeader>
              <CardContent className="p-0">
                <div className="max-h-[calc(100vh-350px)] overflow-y-auto">
                  {rolesLoading ? (
                    // Show skeleton loading for roles
                    Array.from({ length: 4 }).map((_, index) => (
                      <div key={`role-skeleton-${index}`} className="p-4 border-b">
                        <div className="flex items-center justify-between">
                          <div className="flex-1">
                            <Skeleton className="h-4 w-32 mb-2" />
                            <Skeleton className="h-3 w-24 mb-1" />
                            <Skeleton className="h-3 w-48" />
                          </div>
                          <Skeleton className="h-6 w-16" />
                        </div>
                      </div>
                    ))
                  ) : (
                    filteredRoles.map((role) => (
                    <div
                      key={role.id}
                      onClick={() => handleRoleSelect(role)}
                      className={`p-4 border-b cursor-pointer hover:bg-gray-50 transition-colors ${
                        selectedRole?.id === role.id ? 'bg-blue-50 border-l-4 border-l-blue-500' : ''
                      }`}
                    >
                      <div className="flex items-center justify-between">
                        <div>
                          <h3 className="font-medium text-sm">{role.display_name}</h3>
                          <p className="text-xs text-gray-500">{role.name}</p>
                          {role.description && (
                            <p className="text-xs text-gray-400 mt-1 line-clamp-2">{role.description}</p>
                          )}
                        </div>
                        <div className="flex flex-col items-end gap-1">
                          {role.is_system_role && (
                            <Badge variant="secondary" className="text-xs">System</Badge>
                          )}
                          <Badge variant={role.is_active ? "default" : "destructive"} className="text-xs">
                            {role.is_active ? "Active" : "Inactive"}
                          </Badge>
                        </div>
                      </div>
                    </div>
                  ))
                  )}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Right Panel - Pages and Permissions */}
          <div className="lg:col-span-2">
            {selectedRole ? (
              <Card className="h-full">
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <div>
                      <CardTitle className="flex items-center gap-2">
                        <Key className="h-5 w-5" />
                        Page Permissions for {selectedRole.display_name}
                      </CardTitle>
                      <CardDescription>
                        Manage page-level permissions for this role
                      </CardDescription>
                    </div>
                    <Button
                      onClick={handleUpdatePermissions}
                      disabled={isUpdating}
                      className="flex items-center gap-2 bg-purple-600 hover:bg-purple-700 text-white"
                    >
                      {isUpdating ? (
                        <RefreshCw className="h-4 w-4 animate-spin" />
                      ) : (
                        <Save className="h-4 w-4" />
                      )}
                      {isUpdating ? 'Updating...' : 'Save Changes'}
                    </Button>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="max-h-[calc(100vh-400px)] overflow-y-auto">
                    {/* Table Header */}
                    <div className="sticky top-0 bg-white border-b border-gray-200 z-10">
                      <div className="grid grid-cols-6 gap-4 p-4 font-medium text-sm text-gray-700">
                        <div className="col-span-2">Page</div>
                        <div className="text-center flex items-center justify-center gap-1">
                          <Eye className="h-4 w-4" />
                          View
                        </div>
                        <div className="text-center flex items-center justify-center gap-1">
                          <UserPlus className="h-4 w-4" />
                          Add
                        </div>
                        <div className="text-center flex items-center justify-center gap-1">
                          <Edit3 className="h-4 w-4" />
                          Update
                        </div>
                        <div className="text-center flex items-center justify-center gap-1">
                          <Trash className="h-4 w-4" />
                          Delete
                        </div>
                      </div>
                    </div>

                    {/* Table Body */}
                    <div className="divide-y divide-gray-200">
                      {pagesLoading ? (
                        // Show skeleton loading for pages
                        Array.from({ length: 6 }).map((_, index) => (
                          <div key={`page-skeleton-${index}`} className="grid grid-cols-6 gap-4 p-4">
                            <div className="col-span-2 flex items-center gap-3">
                              <Skeleton className="h-4 w-4" />
                              <div className="min-w-0 flex-1">
                                <Skeleton className="h-4 w-32 mb-1" />
                                <Skeleton className="h-3 w-24" />
                              </div>
                            </div>
                            <div className="flex justify-center">
                              <Skeleton className="h-5 w-10" />
                            </div>
                            <div className="flex justify-center">
                              <Skeleton className="h-5 w-10" />
                            </div>
                            <div className="flex justify-center">
                              <Skeleton className="h-5 w-10" />
                            </div>
                            <div className="flex justify-center">
                              <Skeleton className="h-5 w-10" />
                            </div>
                          </div>
                        ))
                      ) : (
                        sortedPages.map((page) => (
                        <div key={page.page_id} className="grid grid-cols-6 gap-4 p-4 hover:bg-gray-50 transition-colors">
                              {/* Page Name Column */}
                              <div className="col-span-2 flex items-center gap-3">
                                <Checkbox
                                  checked={page.has_permission}
                                  onCheckedChange={() => handlePageToggle(page.page_id)}
                                  className="flex-shrink-0 data-[state=checked]:bg-purple-600 data-[state=checked]:border-purple-600"
                                />
                                <div className="min-w-0">
                                  <div className="flex items-center gap-2">
                                    <div className="w-3 h-3 bg-purple-600 rounded-sm flex-shrink-0"></div>
                                    <h4 className="font-medium text-sm text-gray-900 truncate">
                                      {page.page_name}
                                    </h4>
                                  </div>
                                  <p className="text-xs text-gray-500 truncate ml-5">
                                    {page.page_path}
                                  </p>
                               
                                </div>
                              </div>

                              {/* View Permission */}
                              <div className="flex items-center justify-center">
                                <Switch
                                  checked={page.can_view}
                                  onCheckedChange={() => handlePagePermissionToggle(page.page_id, 'can_view')}
                                  disabled={!page.has_permission}
                                  className="data-[state=checked]:bg-purple-600 data-[state=unchecked]:bg-gray-200 disabled:opacity-30 disabled:cursor-not-allowed"
                                />
                              </div>

                              {/* Add Permission */}
                              <div className="flex items-center justify-center">
                                <Switch
                                  checked={page.can_add}
                                  onCheckedChange={() => handlePagePermissionToggle(page.page_id, 'can_add')}
                                  disabled={!page.has_permission}
                                  className="data-[state=checked]:bg-purple-600 data-[state=unchecked]:bg-gray-200 disabled:opacity-30 disabled:cursor-not-allowed"
                                />
                              </div>

                              {/* Update Permission */}
                              <div className="flex items-center justify-center">
                                <Switch
                                  checked={page.can_update}
                                  onCheckedChange={() => handlePagePermissionToggle(page.page_id, 'can_update')}
                                  disabled={!page.has_permission}
                                  className="data-[state=checked]:bg-purple-600 data-[state=unchecked]:bg-gray-200 disabled:opacity-30 disabled:cursor-not-allowed"
                                />
                              </div>

                              {/* Delete Permission */}
                              <div className="flex items-center justify-center">
                                <Switch
                                  checked={page.can_delete}
                                  onCheckedChange={() => handlePagePermissionToggle(page.page_id, 'can_delete')}
                                  disabled={!page.has_permission}
                                  className="data-[state=checked]:bg-purple-600 data-[state=unchecked]:bg-gray-200 disabled:opacity-30 disabled:cursor-not-allowed"
                                />
                              </div>
                        </div>
                      ))
                      )}
                    </div>
                  </div>
                </CardContent>
              </Card>
            ) : (
              <Card className="h-full flex items-center justify-center">
                <CardContent>
                  <div className="text-center text-gray-500">
                    <Shield className="h-12 w-12 mx-auto mb-4 text-gray-300" />
                    <h3 className="text-lg font-medium mb-2">Select a Role</h3>
                    <p className="text-sm">Choose a role from the left panel to manage its page permissions</p>
                  </div>
                </CardContent>
              </Card>
            )}
          </div>
        </div>

        {/* Create Role Dialog */}
        <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Create New Role</DialogTitle>
              <DialogDescription>
                Create a new role with custom permissions
              </DialogDescription>
            </DialogHeader>
            <div className="space-y-4">
              <div>
                <Label htmlFor="role-name">Role Name</Label>
                <Input
                  id="role-name"
                  placeholder="e.g., manager"
                  value={newRole.name}
                  onChange={(e) => setNewRole(prev => ({ ...prev, name: e.target.value }))}
                />
              </div>
              <div>
                <Label htmlFor="role-display-name">Display Name</Label>
                <Input
                  id="role-display-name"
                  placeholder="e.g., Manager"
                  value={newRole.display_name}
                  onChange={(e) => setNewRole(prev => ({ ...prev, display_name: e.target.value }))}
                />
              </div>
              <div>
                <Label htmlFor="role-description">Description</Label>
                <Textarea
                  id="role-description"
                  placeholder="Describe the role's responsibilities..."
                  value={newRole.description}
                  onChange={(e) => setNewRole(prev => ({ ...prev, description: e.target.value }))}
                />
              </div>
              <div className="flex justify-end gap-2">
                <Button variant="outline" onClick={() => setIsCreateDialogOpen(false)}>
                  Cancel
                </Button>
                <Button
                  onClick={handleCreateRole}
                  disabled={!newRole.name || !newRole.display_name || createRoleMutation.isPending}
                >
                  {createRoleMutation.isPending ? 'Creating...' : 'Create Role'}
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </Page>
    </PermissionRoute>
  );
};

export default RolePermissionManagement;
